.libary-css {
    img {
        width: auto;
        max-width: 100%;
        display: block; }
    a {
        color: inherit;
        display: block;
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.8; } }
    button {
        color: inherit;
        background-color: transparent;
        border: none;
        cursor: pointer; }
    button, a , input {
        outline: none; }
    .d-block {
        display: block; }
    .bg-white {
        z-index: 3;
        position: relative;
        &::after {
            content: '';
            z-index: -1;
            background-color: white;
            top: 0px;
            bottom: -2px;
            right: 0px;
            left: 0px;
            position: absolute; } }
